import React from "react"
import {Link} from "gatsby"
import {Col, Container, Row} from "react-bootstrap";

import Layout from "../components/layout"
import SEO from "../components/seo"
import MailChimpForm from "../components/MailchimpForm";
import {FaFacebookF, FaEnvelopeOpenText, FaInstagram, FaYoutube, FaSteam} from 'react-icons/fa';


const ContactPage = () => (
    <Layout pageInfo={{pageName: "contact"}}>
      <SEO title="Contact"/>
      <Row className="d-flex justify-content-around text-center">
        <Col className="text-center border-right align-center find-us-col">
          <h3 className="mb-5">Get in Touch</h3>
          <div className="header-icon">
            <a href='https://www.instagram.com/everreachfarm/' target="_blank">
              <div className="d-flex justify-content-center find-us-links">
                <div className="mr-3"><FaInstagram/></div>
                <div>@EverReachFarm</div>
              </div>
            </a>
          </div>
          <div className="header-icon">
            <a href='https://www.facebook.com/everreachfarm' target="_blank">
              <div className="d-flex justify-content-center find-us-links">
                <div className="mr-3"><FaFacebookF/></div>
                <div>Ever Reach Farm</div>
              </div>
            </a>
          </div>
          <div className="header-icon">
            <a href="mailto:everreachfarm@gmail.com" target="_blank">
              <div className="d-flex justify-content-center find-us-links">
                <div className="mr-3"><FaEnvelopeOpenText/></div>
                <div>everreachfarm@gmail.com</div>
              </div>
            </a>
          </div>
        </Col>
        <Col className="align-center">
          <h3>Join our Newsletter!</h3>
          <p className="mb-5">Sign up here for updates on the farm</p>
          <MailChimpForm/>
        </Col>
      </Row>
    </Layout>
)

export default ContactPage
