import addToMailchimp from 'gatsby-plugin-mailchimp'
import React from "react";

export default class MailChimpForm extends React.Component {
  state = {
    listFields: {
      FNAME: "",
      LNAME: ""
    },
    email: "",
    message: "",
    result: ""
  }

  constructor() {
    super()
    this.state = {email: "", result: null}
  }

  _handleSubmit = async e => {
    e.preventDefault()
    const result = await addToMailchimp(this.state.email, this.state.listFields)
    this.setState({result: result})
    document.getElementById("mailchimp-form").reset();
  }
  _handleChange = e => {
    let inputValue = e.target.value;
    console.log({
      [`${e.target.name}`]: inputValue,
    })
    if (e.target.name === "email") {
      this.setState({
        [`${e.target.name}`]: e.target.value,
      })
    } else if (e.target.name === "FNAME") {
      this.setState(prevState => ({
        listFields: {
          ...prevState.listFields,
          FNAME: inputValue
        }
      }))
    } else if (e.target.name === "LNAME") {
      this.setState(prevState => ({
        listFields: {
          ...prevState.listFields,
          LNAME: inputValue
        }
      }))
    }
    // this.setState({email: e.target.value})
  }

  render() {
    return (
        <form id="mailchimp-form" className="submit-form mt-2" onSubmit={this._handleSubmit}>
          <div className="form-row">
            <div className="form-group col-md-6">
              <input
                  className="form-control mr-sm-2"
                  id="outlined-email-input"
                  label="Name"
                  type="text"
                  name="FNAME"
                  autoComplete="name"
                  variant="outlined"
                  placeholder="First"
                  onChange={this._handleChange}/>
            </div>
            <div className="form-group col-md-6">
              <input
                  className="form-control mr-sm-2"
                  id="outlined-email-input"
                  label="Name"
                  type="text"
                  name="LNAME"
                  autoComplete="name"
                  variant="outlined"
                  placeholder="Last"
                  onChange={this._handleChange}/>
            </div>
          </div>


          <div className="form-row justify-content-center">
            <div className="form-group col-md-8 ">
              <input
                  className="form-control mr-sm-2"
                  id="outlined-email-input"
                  label="Email"
                  type="email"
                  name="email"
                  autoComplete="email"
                  placeholder="Email"
                  variant="outlined"
                  onChange={this._handleChange}/>
            </div>

          </div>

            <button
                className="btn btn-outline-success"
                label="Submit"
                type="submit">
              Subscribe
            </button>
        </form>


    )
  }
}
